<template>
  <aside class="cart-container">
    <div class="cart">
      <h1 class="cart-title spread">
        <span>
          Cart
          <i class="icofont-cart-alt icofont-1x"></i>
        </span>
        <button @click="$emit('toggle')" class="cart-close">&times;</button>
      </h1>

      <div class="cart-body">
        <table class="cart-table">
          <thead>
            <tr>
              <th><span class="sr-only">Product Image</span></th>
              <th>Product</th>
              <th>Price</th>
              <th>Qty</th>
              <th>Total</th>
              <th><span class="sr-only">Actions</span></th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(value, key, index) in cart" :key="index">
              <td><i class="icofont-carrot icofont-3x"></i></td>
              <td>{{ key }}</td>
              <td>${{ getPrice(key) }}</td>
              <td class="center">{{ value }}</td>
              <td>${{ (value * getPrice(key)).toFixed(2) }}</td>
              <td class="center">
                <button @click="$emit('remove', key)" class="btn btn-light cart-remove">
                  &times;
                </button>
              </td>
            </tr>
          </tbody>
        </table>

        <p v-if="!Object.keys(cart).length" class="center"><em>No items in cart</em></p>
        <div class="spread totalSec">
          <span><strong>Total: </strong>${{ calculateTotal() }}</span>
          <button class="btn btn-light">Checkout</button>
        </div>
      </div>
    </div>
  </aside>
</template>

<script>
export default {
  name: 'SideBar',

  props: ['cart', 'inventory'],

  methods: {
    getPrice (name) {
      const product = this.inventory.find((p) => p.name === name)

      return product.price.USD
    },

    calculateTotal () {
      // entries: [key, value]  (curr[0] = key, curr[1] = value)
      const total = Object.entries(this.cart).reduce((acc, cur) => {
        return acc + cur[1] * this.getPrice(cur[0])
      }, 0)

      return total.toFixed(2)
    }
  }
}
</script>
